@charset "urf-8";
/* TodoListTemplate.css */

.todo_list_template{
  width:94%;
  background:#fff;
  margin:4rem auto 0 auto;
  box-shadow:0px 3px 6px #ccc;
}

.title{
  padding:1rem;
  font-size:1.8rem;
  text-align:center;
  background:rgb(255, 115, 0);
  color:#fff;
}

.form_wrapper{
  padding:1rem;
  border-bottom:1px solid rgb(0, 140, 255);
}
.todos_wrapper{
  min-height:5rem;
}