@charset "utf-8";
/*  TodoItem.css */

.todo_item {
  padding: 1rem;
  display: flex;
  align-items: center; /* 세로 가운데 정렬 */
  cursor: pointer;
  cursor:pointer;
  line-height:40px;
  position:relative;
  transition: 0.3s;
}

.todo_item:hover{
  background-color:#ccc;
}
.todo_item:hover .check_mark{
  
  border: 2px solid #0d1b1d;
}
/* todo-item에 마우스가 있을때만 .remove 보이기 */
.todo_item:hover .remove{
  opacity:1;
}

/* todo_item 사이에 윗 테두리 */
.todo_item + .todo_item{
  border-top: 1px solid #f1f3f5;
}
/* 삭제버튼 서식 X */
.remove{
  opacity:0;
  font-weight:bold;
  color:#0d1b1d;
  margin-right:0.5rem;
}
.todo_text{
  flex:1;
  word-break:break-all;
}
/* 해당 글 목록 클릭시 글자 서식이 흐리고, 가운데 줄 표시 하도록 함. */
.checked{
  text-decoration:line-through; /*취소선 서식*/
  color:#adb5bd; /*글자흐리게*/
}
.check_mark{
  font-size:1.5rem;
  line-height:1.5rem;
  color:#fff;
  font-weight:500;
  position:absolute;
  right:3%;
  width:30px;
  text-align: center;
  background: rgb(223, 100, 0);
  border: 2px solid rgb(223, 100, 0);
  box-sizing: border-box;
}



